import {Cell, Group, Header, Switch} from "@vkontakte/vkui";
import React from "react";

const ProfileSettings = ({isPublic, isRating, updateSetting}) => {
    return <div>
        <Group header={<Header mode={'secondary'}></Header>}>
            <Cell
                disabled
                after={<Switch
                    checked={isRating}
                    onChange={() => updateSetting('clearing', !isRating)}
                />}>
                  
            </Cell>
            <Cell
                disabled
                after={<Switch
                    checked={isPublic}
                    onChange={() => updateSetting('publicProfile', !isPublic)}
                />}>
                 
            </Cell>
        </Group>
        <Group header={<Header mode="secondary"></Header>}>
            <Cell onClick={() => window.open("https://vk.me/public207277087", "_blank")}> </Cell>
            <Cell onClick={() => window.open("https://vk.com/public207277087", "_blank")}> </Cell>
        </Group>
    </div>
}

export default ProfileSettings